unset;\r\n            white-space: nowrap;\r\n            overflow-x: unset;\r\n        }\r\n\r\n        .wrap_style {\r\n            max-width: 200px;\r\n            overflow-wrap: break-word;\r\n            white-space: normal;\r\n            vertical-align: middle;\r\n            overflow-x: unset;\r\n            @media screen and (max-width: 768px) {\r\n                & {\r\n                    max-width: unset;\r\n                }\r\n            }\r\n        }\r\n\r\n        ::-webkit-scrollbar {\r\n            width: 10px;\r\n        }\r\n\r\n        ::-webkit-scrollbar-track {\r\n            box-shadow: inset 0 0 10px 10px rgb(34 36 38 / 10%);\r\n            border: solid 5px transparent;\r\n        }\r\n\r\n        ::-webkit-scrollbar-thumb {\r\n            box-shadow: inset 0 0 10px 10px rgb(34 36 38 / 10%);\r\n            border: solid 5px transparent;\r\n        }\r\n\r\n        th:focus {\r\n            outline: none;\r\n        }\r\n    }\r\n    select {\r\n        width: 90px;\r\n        border-radius: 5px;\r\n        border: 1px solid #00000033 !important;\r\n        background: transparent !important;\r\n    }\r\n    .dt-buttons {\r\n        display: flex;\r\n        justify-content: flex-end;\r\n        margin-top: 5px;\r\n        gap: 10px;\r\n        button {\r\n            font-size: 13px !important;\r\n            margin: 0 6px;\r\n            margin-right: 5px;\r\n            &:hover img {\r\n                filter: brightness(0.1);\r\n            }\r\n        }\r\n        span {\r\n            display: flex;\r\n            justify-content: center;\r\n            align-items: center;\r\n            height: 21px;\r\n        }\r\n        .export_btns {\r\n            display: none;\r\n        }\r\n    }\r\n    .gswpts_tables_content {\r\n        table {\r\n            width: 100% !important;\r\n        }\r\n        .filtering_input {\r\n            display: flex;\r\n            justify-content: space-between;\r\n            align-items: center;\r\n            margin: 15px 0 5px 0;\r\n            #create_tables_length {\r\n                margin-right: auto;\r\n            }\r\n            #create_tables_filter {\r\n                margin-left: auto;\r\n            }\r\n            input {\r\n                background: transparent !important;\r\n            }\r\n        }\r\n        .bottom_options {\r\n            display: flex;\r\n            justify-content: space-between;\r\n            align-items: center;\r\n            margin-top: 10px;\r\n            .dataTables_info {\r\n                padding-top: 0 !important;\r\n                margin-right: auto;\r\n            }\r\n            #create_tables_paginate {\r\n                margin-left: auto;\r\n                padding: 0 5px;\r\n            }\r\n            .pagination.menu {\r\n                padding: 0 !important;\r\n                font-size: 14px;\r\n                background: transparent !important;\r\n                display: flex;\r\n                justify-content: center;\r\n                align-items: center;\r\n                flex-wrap: wrap;\r\n                border: none;\r\n                margin: 0 5px;\r\n\r\n                & > * {\r\n                    padding: 9px 16px;\r\n                    border: 1px solid #a9a1a13d;\r\n                    -webkit-box-shadow: 1px 1px 0px 0px rgba(145, 145, 145, 0.48);\r\n                    box-shadow: 1px 1px 0px 0px rgba(145, 145, 145, 0.48);\r\n                    &:first-child {\r\n                        border-radius: 0.28571429rem 0px 0px 0.28571429rem;\r\n                    }\r\n                    &:last-child {\r\n                        border-radius: 0px 0.28571429rem 0.28571429rem 0px 0px;\r\n                    }\r\n                    font-weight: bold;\r\n                    color: rgba(56, 13, 13, 0.787);\r\n\r\n                    &:hover {\r\n                        background-color: rgba(0, 0, 0, 0.03);\r\n                    }\r\n                }\r\n                a {\r\n                    text-decoration: none;\r\n                }\r\n                .active {\r\n                    background-color: #b3b3b35c;\r\n                }\r\n            }\r\n        }\r\n    }\r\n    @media screen and (max-width: 540px) {\r\n        .dt-buttons {\r\n            display: flex;\r\n            flex-wrap: wrap;\r\n            justify-content: space-between;\r\n            button {\r\n                font-size: 10px !important;\r\n                margin: 10px 0;\r\n            }\r\n        }\r\n        .filtering_input {\r\n            flex-direction: column !important;\r\n            align-items: center !important;\r\n            justify-content: center !important;\r\n            div {\r\n                margin: 10px 0 !important;\r\n                padding: 0 !important;\r\n            }\r\n        }\r\n    }\r\n    @media screen and (max-width: 700px) {\r\n        .dt-buttons {\r\n            display: flex;\r\n            flex-wrap: wrap;\r\n            justify-content: space-between;\r\n            button {\r\n                font-size: 12px !important;\r\n                margin: 10px 0;\r\n            }\r\n        }\r\n        .bottom_options {\r\n            flex-direction: column !important;\r\n            justify-content: center !important;\r\n            align-items: center !important;\r\n\r\n            .dataTables_info {\r\n                padding-top: 0 !important;\r\n                margin: 0 !important;\r\n            }\r\n            #create_tables_paginate {\r\n                margin: 0 !important;\r\n                margin-top: 8px !important;\r\n            }\r\n        }\r\n    }\r\n\r\n    @media screen and (max-width: 768px) {\r\n        &.collapse_style {\r\n            table .expanded_style {\r\n                white-space: normal;\r\n            }\r\n        }\r\n    }\r\n}\r\n","@mixin button_style {\r\n    .ui.button {\r\n        cursor: pointer;\r\n        display: inline-block;\r\n        min-height: 1em;\r\n        outline: none;\r\n        border: none;\r\n        vertical-align: baseline;\r\n        background: #e0e1e2 none;\r\n        color: rgba(0, 0, 0, 0.6);\r\n        font-family: \"Lato\", \"Helvetica Neue\", Arial, Helvetica, sans-serif;\r\n        margin: 0em 0.25em 0em 0em;\r\n        padding: 8px 12px;\r\n        text-transform: none;\r\n        text-shadow: none;\r\n        font-weight: bold;\r\n        line-height: 1em;\r\n        font-style: normal;\r\n        text-align: center;\r\n        text-decoration: none;\r\n        border-radius: 0.28571429rem;\r\n        -webkit-box-shadow: 0px 0px 0px 1px transparent inset,\r\n            0px 0em 0px 0px rgba(34, 36, 38, 0.15) inset;\r\n        box-shadow: 0px 0px 0px 1px transparent inset, 0px 0em 0px 0px rgba(34, 36, 38, 0.15) inset;\r\n        -webkit-user-select: none;\r\n        -moz-user-select: none;\r\n        -ms-user-select: none;\r\n        user-select: none;\r\n        -webkit-transition: opacity 0.1s ease, background-color 0.1s ease, color 0.1s ease,\r\n            background 0.1s ease, -webkit-box-shadow 0.1s ease;\r\n        transition: opacity 0.1s ease, background-color 0.1s ease, color 0.1s ease,\r\n            background 0.1s ease, -webkit-box-shadow 0.1s ease;\r\n        transition: opacity 0.1s ease, background-color 0.1s ease, color 0.1s ease,\r\n            box-shadow 0.1s ease, background 0.1s ease;\r\n        transition: opacity 0.1s ease, background-color 0.1s ease, color 0.1s ease,\r\n            box-shadow 0.1s ease, background 0.1s ease, -webkit-box-shadow 0.1s ease;\r\n        will-change: \"\";\r\n        -webkit-tap-highlight-color: transparent;\r\n    }\r\n    /*******************************\r\n            States\r\n*******************************/\r\n    /*--------------\r\n      Hover\r\n---------------*/\r\n    .ui.button:hover {\r\n        background-color: #cacbcd;\r\n        background-image: none;\r\n        -webkit-box-shadow: 0px 0px 0px 1px transparent inset